import { EditType, Cursor, TargetDirection } from '../shared/enum'
import { NodeErwin } from '../../core/materials/node'
import { NodeRect } from '../../core/materials/node'
import { LineErwin } from '../../core/materials/line'
import { TextBase } from '../../core/materials/text'
import { isPointOnLineSegments } from '../../core/utils/fx'
import type { Erg } from '../index'

/**
   * 
   */
const ifAnchor = (target: NodeErwin, canvasEv) => {
  // 
  const { x, y, width, height } = target
  const middleX = x + width / 2
  const middleY = y + height / 2
  const fullX = x + width
  const fullY = y + height
  const tolerance = 5
  let targetDirection = TargetDirection.NONE

  if (Math.abs(x - canvasEv.x) <= tolerance && Math.abs(y - canvasEv.y) <= tolerance) targetDirection = TargetDirection.LT
  else if (Math.abs(middleX - canvasEv.x) <= tolerance && Math.abs(y - canvasEv.y) <= tolerance) targetDirection = TargetDirection.TM
  else if (Math.abs(fullX - canvasEv.x) <= tolerance && Math.abs(y - canvasEv.y) <= tolerance) targetDirection = TargetDirection.RT
  else if (Math.abs(x - canvasEv.x) <= tolerance && Math.abs(middleY - canvasEv.y) <= tolerance) targetDirection = TargetDirection.LM
  else if (Math.abs(fullX - canvasEv.x) <= tolerance && Math.abs(middleY - canvasEv.y) <= tolerance) targetDirection = TargetDirection.RM
  else if (Math.abs(x - canvasEv.x) <= tolerance && Math.abs(fullY - canvasEv.y) <= tolerance) targetDirection = TargetDirection.LB
  else if (Math.abs(middleX - canvasEv.x) <= tolerance && Math.abs(fullY - canvasEv.y) <= tolerance) targetDirection = TargetDirection.BM
  else if (Math.abs(fullX - canvasEv.x) <= tolerance && Math.abs(fullY - canvasEv.y) <= tolerance) targetDirection = TargetDirection.RB

  return targetDirection
}



export const useMouseMove = function (this: Erg, e, canvasEv) {
  /**
   * 
   */
  const moveMarkRect = () => {
    const {
      eventObj,
    } = this
    const offsetOfMove = {
      x: canvasEv.x - eventObj.targetOffsetX - eventObj.target.x,
      y: canvasEv.y - eventObj.targetOffsetY - eventObj.target.y,
    }
    const newLoc = {
      x: eventObj.target.x + offsetOfMove.x,
      y: eventObj.target.y + offsetOfMove.y,
    }

    this.markMap.get(eventObj.target.id).updateOptions(newLoc)
    this.emit('mark:move', this.markMap.get(eventObj.target.id))
    this.update()
  }
  

  /**
   * 
   */
  const moveMarkText = () => {
    const {
      eventObj,
    } = this

    const offsetOfMove = {
      x: canvasEv.x - eventObj.targetOffsetX - eventObj.target.x,
      y: canvasEv.y - eventObj.targetOffsetY - eventObj.target.y,
    }

    const newLoc = {
      x: eventObj.target.x + offsetOfMove.x,
      y: eventObj.target.y + offsetOfMove.y,
    }
    this.noteMap.get(eventObj.target.id).updateOptions(newLoc)

    this.emit('note:move', eventObj.target)
    this.update()
  }

  /**
   * 
   */
  const resizeNode = () => {
    const {
      eventObj,
    } = this
    // eventObj.target.manualChange = true
    const {x, y} = canvasEv
    if(eventObj.target.type === 'mark'){
      this.resizeNode(x, y, eventObj,null)
    }else{
      this.resizeNode(x, y, eventObj)
    }
    
    this.update()
  }

  /**
   * 
   */
  const moveNode = () => {
    const {
      focusTarget,
      eventObj,
      nodeRelatedLineMap,
    } = this

    const offsetOfMove = {
      x: canvasEv.x - eventObj.targetOffsetX - eventObj.target.x,
      y: canvasEv.y - eventObj.targetOffsetY - eventObj.target.y,
    }

    focusTarget.forEach(focusItem => {
      const nodeNewLoc = {
        x: focusItem.x + offsetOfMove.x,
        y: focusItem.y + offsetOfMove.y,
      }

      // 
      if (nodeRelatedLineMap.has(focusItem.id)) {
        const involveLines = [...nodeRelatedLineMap.get(focusItem.id).from, ...nodeRelatedLineMap.get(focusItem.id).to]
        involveLines.forEach(line => {
          line.from === line.to
            ? this.moveLineForSameNode(line, { target: focusItem })
            : this.moveLineForNode(line, { target: focusItem }, nodeNewLoc)
        })
      }

      this.nodeMap.get(focusItem.id).updateOptions(nodeNewLoc)
      this.emit('node:move', focusItem)
    })

    this.update()
  }

  /**
   * 
   */
  const moveLine = () => {
    const {
      eventObj,
    } = this

    const idx = eventObj.target.getIndexInPos(eventObj.lastEvLoc)
    eventObj.targetLineStartPointIdx = idx
    eventObj.lastEvLoc = canvasEv
    if (idx === false) return
    this.moveLineForLine(eventObj, canvasEv)
    this.update()
  }

  /**
   * 
   */
  const moveCanvas = () => {
    this.canvas.setTranslate(e)
    this.canvas.clearEventCanvas()
    const graphTranslate = this.canvas.getTranslate()
    this.emit('graph:move', graphTranslate)
    this.update()
  }

  /**
   * 
   */
  const changeMouseCursor = () => {
    const {
      editType,
      renderElements,
    } = this

    const cursorMap = {
      [EditType.addNode]: Cursor.cell,
      [EditType.primaryKey]: Cursor.crosshair,
      [EditType.foreignKey]: Cursor.crosshair,
      [EditType.branchLine]: Cursor.crosshair,
      [EditType.addMark]: Cursor.cell,
      [EditType.addNote]: Cursor.text,
      [EditType.copy]: Cursor.copy,
    }

    const cursorState = cursorMap[editType]
    // 
    if (cursorState) return this.canvas.eventEl.style.cursor = cursorState || Cursor.default

    let hoverTarget = null, cursor = Cursor.default

    // 
    for (let i = renderElements.length - 1; i >= 0; i--) {
      const el = renderElements[i]
      const isTarget = el.containsPoint(canvasEv) || ifAnchor(el, canvasEv)
      if (isTarget) {
        hoverTarget = el
        
        cursor = isTarget === TargetDirection.LT ? Cursor.nwseResize
          : isTarget === TargetDirection.TM ? Cursor.nsResize
          : isTarget === TargetDirection.RT ? Cursor.neswResize
          : isTarget === TargetDirection.LM ? Cursor.ewResize
          : isTarget === TargetDirection.RM ? Cursor.ewResize
          : isTarget === TargetDirection.LB ? Cursor.neswResize
          : isTarget === TargetDirection.BM ? Cursor.nsResize
          : isTarget === TargetDirection.RB ? Cursor.nwseResize
          : Cursor.move
        break
      }
    }

    // 
    this.hoverTarget = hoverTarget
    this.canvas.eventEl.style.cursor = cursor
    this.updateEventCanvas()
  }

  /**
   * 
   */
  const moveElement = () => {
    const {
      focusTarget,
      eventObj,
    } = this

    const isResizeNode = !!eventObj.targetDirection
    const isNodeMove = eventObj.target instanceof NodeErwin
    const isLineMove = eventObj.target instanceof LineErwin
    const isMarkMove = eventObj.target instanceof NodeRect
    const isNoteMove = eventObj.target instanceof TextBase
    if (isResizeNode) return resizeNode()
    if (isNodeMove) return moveNode()
    if (isLineMove) return moveLine()
    if (isMarkMove) return moveMarkRect()
    if (isNoteMove) return moveMarkText()
  }
  
  return {
    changeMouseCursor,
    moveCanvas,
    moveElement,
  }
}

export const useMouseDown = function (this: Erg, e, canvasEv) {
  const { x: evX, y: evY } = canvasEv

  const execTask = () => {
    const {editType, defaultWidth, defaultHeight} = this
    switch (editType) {
      case EditType.copy:
        const distanse = {
          x: evX - this.copyTarget?.[0]?.x,
          y: evY - this.copyTarget?.[0]?.y,
        }

        const fixLocNodes = this.copyTarget.filter(it => !!it.attributes).map(node => {
          node.x = node.x + distanse.x
          node.y = node.y + distanse.y
          return node
        })

        this.externalFn?.handlePaste(fixLocNodes)
        this.editType = ''
        this.canvas.eventEl.style.cursor = Cursor.default
        this.update()
        break
      case EditType.addNode:
        const newNode = Object.assign(this.addNodeDefaultAttr, {
          x: evX - defaultWidth / 2, y: evY - defaultHeight / 2,
          width: defaultWidth,
          height: defaultHeight,
        })

        this.externalFn.addNode(newNode)
        this.addNodeDefaultAttr = {}
        this.editType = ''
        this.canvas.eventEl.style.cursor = Cursor.default
        this.update()
        break
      case EditType.addMark:
        const newMark = {
          key: this.createKey(),
          x: evX, y: evY,
          width: defaultWidth,
          height: defaultHeight,
          type: 'mark',
        }

        this.externalFn.addMark(newMark)
        this.editType = ''
        this.canvas.eventEl.style.cursor = Cursor.default
        this.update()
        break
      case EditType.addNote:
        const newNote = {
          key: this.createKey(),
          x: evX, y: evY,
          text: '',
          type: 'note',
        }

        this.externalFn.addNote(newNote)
        this.editType = ''
        this.canvas.eventEl.style.cursor = Cursor.default
        break
      // case EditType.primaryKey:

      //   break
      // case EditType.foreignKey:

      //   break
      // case EditType.branchLine:

      //   break
    
      default:
        break
    }
  }

  const setEventTarget = (e: MouseEvent) => {
    const {
      metaKey, shiftKey, ctrlKey,
    } = e
    const {
      renderElements,
      focusTarget,
      eventObj,
    } = this

    if (!ctrlKey && !metaKey) focusTarget.clear()

    this.canvas.eventEl.style.cursor = Cursor.grabbing
    for (let i = 0; i < renderElements.length; i++) {
      const el = renderElements[i]
      const targetDirection = ifAnchor(el, canvasEv)
      const isTarget = el.containsPoint(canvasEv, false) || !!targetDirection
      if (!isTarget) continue

      eventObj.target = el
      if (el instanceof LineErwin) {
        focusTarget.clear()
        eventObj.lastEvLoc = canvasEv
      }
      else if (el instanceof NodeErwin || el instanceof TextBase || el instanceof NodeRect) {
        eventObj.targetOffsetX = canvasEv.x - el.x
        eventObj.targetOffsetY = canvasEv.y - el.y
        eventObj.targetDirection = targetDirection
      }
      focusTarget.add(el)
      break
    }
    
    this.update()
  }
  
  return {
    execTask,
    setEventTarget,
  }
}

export const useMouseUp = function (this: Erg, e, canvasEv) {

  const resetMouseState = () => {
    this.canvas.eventEl.style.cursor = Cursor.default
    this.eventObj = {
      target: null,
      targetDirection: TargetDirection.NONE,
      targetOffsetX: 0,
      targetOffsetY: 0,
    }
  }
  return {
    resetMouseState,
  }
}