const makeManyNodes = (nodeNumber = 10000) => {
  for (let i = 0; i < nodeNumber; i++) {
    defaultData.nodeDataArray.push({
      key: 'q000' + i,
      x: 100 + i * 100, y: 100 + i * 100,
      width: 100,
      height: 50,
      bgC: '#FF8080',
      bdC: 'red',
      tableName: '' + i,
      physicalTables: true, //
      quotePrimaryKey: true, //
      attributes: defaultData.nodeDataArray[0].attributes,
      checkoutInfo: {},
    })
  }
}
const makeManyNodeAttrs = (attrNumber = 500) => {
  for (let i = 0; i < attrNumber; i++) {
    defaultData.nodeDataArray[0].attributes.push({
      attrName: `${i}`,
      primaryKey: true,
    })
  }
}
// 
export const useBigData = () => {
  makeManyNodes()
  makeManyNodeAttrs()
}
