import { FabricObject } from '../../shared/fabric'
import { CanvasEvent } from '../../../types'

/**
 * 
 * 1.
 */
interface NodeInput {
  x?: number
  y?: number
  width?: number
  height?: number
  bdC?: string
}
export class FrameHover extends FabricObject {
  private ctx: CanvasRenderingContext2D
  public width = 100
  public height = 50
  public x = 0
  public y = 0
  private borderColor = 'blue'

  constructor(ctx: CanvasRenderingContext2D, input: NodeInput) {
    super()
    this.ctx = ctx
    this.updateOptions(input)
  }

  private analysisOptions(input: NodeInput) {
    if (!input) return

    const {
      x = 0,
      y = 0,
      width,
      height,
      bdC,
    } = input

    if (x) this.x = x
    if (y) this.y = y
    if (width) this.width = width
    if (height) this.height = height
    if (bdC) this.borderColor = bdC
  }

  public updateOptions(input: NodeInput) {
    this.analysisOptions(input)
    this.calcBoundingBox()
  }

  public containsPoint(e: CanvasEvent, trigger = false) {
    return false
  }

  public draw(input?: NodeInput) {
    this.updateOptions(input)

    this.drawBorder()
  }

  private drawBorder() {
    const {
      ctx,
      borderColor,
      x, y, width, height,
    } = this

    ctx.save()
    ctx.strokeStyle = borderColor
    ctx.strokeRect(x, y, width, height)
    ctx.restore()
  }

  private calcBoundingBox() {
    const {
      x, y,
      width, height,
    } = this

    this.boundingBox = [
      [x, y],
      [x + width, y],
      [x, y + height],
      [x + width, y + height],
    ]
  }
}
