<template>
  <div class="erg">
    <div class="toobar">
      <el-button size="small" circle title="" @click="addNode">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#icon-tool-add`"></use>
          </svg>
        </template>
      </el-button>
      <el-button size="small" circle title="" @click="copy">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#copy`"></use>
          </svg>
        </template>
      </el-button>
      <el-button size="small" circle title="" @click="addLine('primaryKey')">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#icon-tool-primary`"></use>
          </svg>
        </template>
      </el-button>
      <el-button size="small" circle title="" @click="addLine('foreignKey')">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#icon-tool-foreign`"></use>
          </svg>
        </template>
      </el-button>
      <el-button size="small" circle title="" @click="addLine('branchLine')">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#icon-tool-subtype`"></use>
          </svg>
        </template>
      </el-button>

      <el-divider direction="vertical" />

      <el-button size="small" circle title="" @click="addMark">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#icon-tool-square`"></use>
          </svg>
        </template>
      </el-button>
      <el-button size="small" circle title="" @click="addNote">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#icon-tool-text`"></use>
          </svg>
        </template>
      </el-button>

      <el-divider direction="vertical" />

      <el-button v-if="state.lock === 1" size="small" circle title="" @click="setEditable">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#lock`"></use>
          </svg>
        </template>
      </el-button>
      <el-button v-else size="small" circle title="" @click="setEditable">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#unlock`"></use>
          </svg>
        </template>
      </el-button>
      <el-button size="small" circle title="" @click="createLines">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#copy`"></use>
          </svg>
        </template>
      </el-button>
      <el-button size="small" circle title="" @click="changeCanvasScale(20)">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#plus-circle`"></use>
          </svg>
        </template>
      </el-button>
      <el-button size="small" circle title="" @click="changeCanvasScale(-20)">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#minus-circle`"></use>
          </svg>
        </template>
      </el-button>

      <el-divider direction="vertical" />
      
      <el-color-picker v-model="state.bgc" size="small" @change="(value) => changeColor(value, 'bgC')" />
      <el-color-picker v-model="state.bdC" size="small" @change="(value) => changeColor(value, 'bdC')" />
      <el-color-picker v-model="state.color" size="small" @change="(value) => changeColor(value, 'color')" />
    </div>
    <div id="canvas" class="canvas"></div>
  </div>
</template>

<script lang="ts" setup>
import {reactive, onBeforeUnmount, onMounted, watch, onUnmounted} from 'vue'
import {ElButton, ElDivider, ElColorPicker} from 'element-plus'
import {Erg as Graph} from '@ej/erg-renderer/src'
import {defaultData as data} from './mock'
import {rightMenu} from './right-menu'
import Icons from './assets/icon/symbol-defs.svg'

const props = withDefaults(defineProps<{
  useThumbnail: boolean,
  useRightMenu: boolean,
  useAttrFocus: boolean,
  editable: number,
}>(), {
  useThumbnail: true,
  useRightMenu: true,
  useAttrFocus: true,
  editable: 1,
})

let graph
const nodeMap = new Map()
const lineMap = new Map()
const noteMap = new Map()
const markMap = new Map()

const state = reactive({
  scale: 100,
  bgC: '',
  bdC: '',
  color: '',
  lock: 1,
})

const initData = () => {
  for (let i = 0; i < data.nodeDataArray.length; i++) {
    const it = data.nodeDataArray[i]
    nodeMap.set(it.key, it)
  }
  for (let i = 0; i < data.linkDataArray.length; i++) {
    const it = data.linkDataArray[i]
    lineMap.set(it.key, it)
  }
  for (let i = 0; i < data.noteDataArray.length; i++) {
    const it = data.noteDataArray[i]
    noteMap.set(it.key, it)
  }
  for (let i = 0; i < data.markDataArray.length; i++) {
    const it = data.markDataArray[i]
    markMap.set(it.key, it)
  }
  
}
const mountGraph = () => {
  // const el = document.getElementById('canvas')
  graph = new Graph({
    el: document.getElementById('canvas'),
    fontFamily: '-apple-system, Noto Sans, Helvetica Neue, Helvetica, Nimbus Sans L, Arial, Liberation Sans, PingFang SC, Hiragino Sans GB, Noto Sans CJK SC, Source Han Sans SC, Source Han Sans CN, Microsoft YaHei, Wenquanyi Micro Hei, WenQuanYi Zen Hei, ST Heiti, SimHei, WenQuanYi Zen Hei Sharp, sans-serif',
    editable: state.lock,
    externalFn: {
      addNode: function (obj) {
        const str = prompt('')
        if (!str) return
        obj.tableName = str
        data.nodeDataArray.push(obj)
      },
      delNode: function (key) {
        console.log(key)
      },
      addLine: function (obj) {
        console.log('')
        obj.numLineTypes = 2
        data.linkDataArray.push(obj)
      },
      delLine: function (key) {
        console.log(key)
      },
      addFilter: function (obj) {
        obj.filter && data.nodeDataArray.push(obj.filter)
        obj.toFilter && data.linkDataArray.push(obj.toFilter)
        data.linkDataArray.push(obj.fromFilter)
      },
      delFilter: function (key) {
        console.log(key)
      },
      addMark: function (obj) {
        data.markDataArray.push(obj)
        markMap.set(obj.key, obj)
      },
      delMark: function (key) {
        console.log(key)
      },
      addNote: function (obj) {
        const str = prompt('')
        if (!str) return
        obj.text = str
        data.noteDataArray.push(obj)
      },
      delNote: function (key) {
        console.log(key)
      },
      $message: function (val) {
        console.log(val.message)
      },
      handleCopy(ev1, ev2) {
        console.log(ev1, ev2)
        return true
      },
      handlePaste(nodes) {
        data.nodeDataArray.push(...nodes)
      },
      handleContextMenu(ev, value) {
        console.log('', ev, value)

        switch (value) {
          case 'remark':
            const str = prompt('')
            if (!str) return
            graph.addRemark(str)
            break

          default:
            break
        }
      },
    },
    useThumbnail: props.useThumbnail,
    useAttrFocus: props.useAttrFocus,
    useRightMenu: props.useRightMenu,
    rightMenu,
  })
  graph.update(data)
  graph.on('node:move', (node) => {
    const it = nodeMap.get(node.id)
    it.x = node.x
    it.y = node.y
    it.width = node.width
    it.height = node.height
  })
  graph.on('node:resize', (node) => {
    const it = nodeMap.get(node.id)
    it.x = node.x
    it.y = node.y
    it.width = node.width
    it.height = node.height
  })
  graph.on('line:move', (line) => {
    const it = lineMap.get(line.id)
    it.pos = line.pos
  })
  graph.on('note:move', (note) => {
    const it = noteMap.get(note.id)
    it.x = note.x
    it.y = note.y
  })
  graph.on('mark:move', (node) => {
    const it = markMap.get(node.id)
    it.x = node.x
    it.y = node.y
    it.width = node.width
    it.height = node.height
  })
  graph.on('mark:resize', (node) => {
    const it = markMap.get(node.id)
    it.x = node.x
    it.y = node.y
    it.width = node.width
    it.height = node.height
  })
  // graph.on('line:click', (id) => console.log('line:click', id))
  // graph.on('node:click', (id) => console.log('node:click', id))
  // graph.on('node-attr:click', (id) => console.log('node-attr:click', id))
}
const unmountGraph = () => {
  graph.destroy()
}
const handleFullscreen = () => {
  graph.fullscreen()
}

const changeCanvasScale = (scaleRatio) => {
  if (state.scale > 20) state.scale += scaleRatio
  graph.zoom(state.scale)
}

const addNode = () => {
  graph.addNode({
    key: graph.createKey(),
    attributes: []
  })
}

const addLine = (lineType) => {
  graph.addLine(lineType)
}

const addMark = () => {
  graph.addMark()
}

const addNote = () => {
  graph.addNote()
}

const changeColor = (value, tp) => {
  graph.changeColor(value, tp)
}

const setEditable = () => {
  state.lock = state.lock === 1 ? 2 : 1
  graph.setEditable(state.lock)
}

const copy = () => {
  graph.copy()
}

const createLines = () => {
  const list = [
    { "from": 0, "to": 1, "pos": [{ x: 200, y: 125 }, { x: 400, y: 125 }], type: 'primaryKey', numLineTypes: 1 },
  ]
  graph.createLines(list)
}

onMounted(() => {
  initData()
  mountGraph()
})
onBeforeUnmount(unmountGraph)
</script>

<style lang="postcss">
.erg{
  height: 100%;
  display: flex;
  flex-direction: column;
}

.toobar {
  background: #ccc;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-between: center;
  >.el-color-picker{
    margin-right: 10px;
  }
}

.canvas {
  flex-grow: 1;
  width: 100%;
  height: 100%;
  /* border: 1px solid #000; */
  margin: auto;
  box-sizing: border-box;
}
</style>