import { FabricObject } from '../../shared/fabric'
import { Table, Attribute, CanvasEvent, Id } from '../../../types'
import { DataTypeClassify, DataTypeDate, DataTypeNumber, DataTypeString } from '../../../types/enum'
import { imageMap } from '../../shared/image'
import { isBoolean, isNumber } from '../../utils/verify'
import { isLightColor } from '../../utils/color'

/**
 * 
 * 1.
 */
interface NodeInput extends Table {
  x?: number
  y?: number
  width?: number
  height?: number
  bgC?: string
  bdC?: string
  quotePrimaryKey?: boolean
  physicalTables?: boolean
  manualChange?: boolean
}
export class NodeErwin extends FabricObject {
  private ctx: CanvasRenderingContext2D
  public width = 100
  public height = 50
  public titleHeight = 24
  private fontSize = 16
  private padding = 10

  private attrHeight = 16
  private borderWidth = 1
  private datatypeIconWidth = 14

  public x = 0
  public y = 0
  public id: Id
  public enName: string
  public cnName: string
  public type: string
  public attributes: Attribute[] = []
  public attributesPrimary: Attribute[] = []
  public attributesPrimaryNon: Attribute[] = []
  private expandAttrs = []

  private borderColor = '#000000'
  private textColor = '#000000'
  private bgColor = '#FFFFFF'
  private hoverColor = '#477DE9'
  private attrFocusStyle = {
    color: '#fff',
    backgroundColor: '#0000ff',
  }

  private quotePrimaryKey = false
  private physicalTables = false
  private manualChange = false

  constructor(ctx: CanvasRenderingContext2D, input: NodeInput) {
    super()
    this.ctx = ctx
    this.updateOptions(input)
  }

  private analysisOptions(input: NodeInput) {
    if (!input) return

    const {
      x,
      y,
      width,
      height,
      id,
      enName,
      cnName,
      type,
      attributes,
      bgC, bdC,
      quotePrimaryKey,
      physicalTables,
      manualChange,
    } = input

    if (isNumber(x)) this.x = x
    if (isNumber(y)) this.y = y
    if (id) this.id = id
    if (enName) this.enName = enName
    if (cnName) this.cnName = cnName
    if (isNumber(width)) this.width = width
    if (isNumber(height)) this.height = height
    if (type) this.type = type
    if (attributes) {
      this.attributes = attributes
      this.attributesPartition()
    }
    if (bgC) this.bgColor = bgC
    if (bdC) this.borderColor = bdC
    if (isBoolean(quotePrimaryKey)) this.quotePrimaryKey = quotePrimaryKey
    if (isBoolean(physicalTables)) this.physicalTables = physicalTables
    if (isBoolean(manualChange)) this.manualChange = manualChange
  }

  public updateOptions(input: NodeInput) {
    this.analysisOptions(input)
    this.calcBoundingBox()
  }

  public containsPoint(e: CanvasEvent, trigger = false) {
    const {
      x: ex, y: ey,
    } = e
    const { x, y, width: w, height: h, attrHeight, titleHeight, attributes } = this

    // 
    const linesForNode = this.getImageLines({
      tl: { x, y: y },
      tr: { x: x + w, y: y },
      bl: { x, y: y + h },
      br: { x: x + w, y: y + h },
    })
    const xpointsNode = this.findCrossPoints(ex, ey, linesForNode)
    const isContainsNode = Boolean(xpointsNode % 2)
    if (!isContainsNode) return false

    return true
    // const relativeH = ey - y

    // // 
    // const attrIndex = Math.floor((relativeH - titleHeight) / attrHeight)
    // return attributes[attrIndex]?.id
  }

  public draw(input?: NodeInput) {
    this.updateOptions(input)

    this.drawContainer()
    this.drawTitle()
    this.drawbody()
  }

  private drawContainer() {
    const {
      ctx,
      x, y,
      width: w, height: h,
      enName, cnName, type,
      attributes,
      bgColor, borderColor, textColor,
      quotePrimaryKey, physicalTables,
    } = this

    ctx.save()
    ctx.strokeStyle = borderColor
    ctx.fillStyle = bgColor
    //
    if (['exclusive', 'inclusive'].includes(type)) {
      ctx.beginPath()
      ctx.arc(x + w / 2, y + w / 2, w / 2, 0, Math.PI, true)
      ctx.closePath()
      ctx.fill()
      ctx.stroke()

      if (type === 'exclusive') {
        ctx.moveTo(x + 12, y + 6)
        ctx.lineTo(x + w - 12, y + h - 3)
        ctx.moveTo(x + w - 12, y + 6)
        ctx.lineTo(x + 12, y + h - 3)
        ctx.stroke()
      }
    }
    //
    else {
      const roundedDeg = quotePrimaryKey ? 4 : 0
      ctx.drawRoundedRect(x, y, w, h, roundedDeg, roundedDeg, roundedDeg, roundedDeg, true, true, physicalTables, true)
    }

    // //
    // attributes && this._drawBody(item)

    //
    // if (this.focusTarget.includes(item) && !['exclusive', 'inclusive'].includes(item.type)) this._drawFocus(x, y, w, h)

    ctx.restore()
  }

  private drawDataType(datatype: DataTypeDate | DataTypeNumber | DataTypeString, x: number, y: number) {
    const datatypeUpper = datatype?.toLocaleUpperCase()
    let img
    if (datatypeUpper in DataTypeDate) {
      img = imageMap.get('date')
    } else if (datatypeUpper in DataTypeNumber) {
      0
      img = imageMap.get('number')
    } else if (datatypeUpper in DataTypeString) {
      img = imageMap.get('string')
    }
    if (!img) return false
    const { ctx } = this
    ctx.drawImage(img, x - 6, y - 2)
  }

  private drawTitle() {
    const {
      ctx,
      textColor,
      enName,
      width, x, y, titleHeight,
      fontSize
    } = this

    ctx.save()
    // 
    ctx.font = `${fontSize}px`
    // 
    ctx.fillStyle = textColor
    // 
    ctx.textAlign = 'left'
    // 
    ctx.textBaseline = 'top'
    ctx.fillText(ctx.fittingString(enName, width - 14), x, y - titleHeight)
    ctx.restore()
  }

  private drawbody () {
    const {
      ctx,
      x, y,
      bgColor: bgC,
      fontSize, padding, attrHeight,
      attributesPrimary, attributesPrimaryNon,
    } = this

    ctx.save()
    // 
    ctx.font = `${fontSize}px`
    // 
    ctx.fillStyle = isLightColor(bgC) ? '#000' : '#fff'
    // 
    ctx.textAlign = 'left'
    // 
    ctx.textBaseline = 'top'

    const curX = padding + x
    let curY = y + padding - attrHeight

    //
    for (let i = 0; i < attributesPrimary.length; i++) {
      const item = attributesPrimary[i]
      curY = this.drawAttrRow(item, curX, curY)
    }

    ///
    curY = this.drawSplitLine(curY)

    //
    for (let i = 0; i < attributesPrimaryNon.length; i++) {
      const item = attributesPrimaryNon[i]
      curY = this.drawAttrRow(item, curX, curY)
    }

    ctx.restore()
  }

  private drawAttrRow(attrItem, curX, curY) {
    const {
      ctx,
      attrHeight, padding, fontSize,
      x, y, width: w, height: h,
    } = this

    curY += attrHeight

    // 
    if (curY + 16 > h + y) return

    // 
    // if (!this.canvas.isVisible([[curX, curY], [curX + w, curY + fontSize]])) return

    // FK
    const addText = attrItem.foreignKey ? '(FK)' : ''
    const finalText = ctx.fittingString(attrItem.attrName + addText, w - (curX - x) - padding)
    // 
    ctx.fillText(finalText, curX, curY)

    // this.drawFocusAttr({
    //   attrItem,
    //   finalText,
    //   curX,
    //   curY,
    //   fullWidth: w - (curX - x),
    //   nodePadding: padding,
    // })

    // icon
    const textWidth = curX + ctx.measureText(finalText).width
    this.drawIcons(attrItem.icons, textWidth, curY)

      if (attrItem.children?.length) {
        // 
        const isExpand = this.expandAttrs.includes(attrItem)

        // 
        this.drawTreeExpandArrow.call(this, {
          x: curX,
          y: curY,
          size: fontSize,
          expand: isExpand,
        })

        // 
        isExpand && attrItem.children.forEach(attrItem => {
          curY = this.drawAttrRow(attrItem, curX + 10, curY)
        })
      }
    
    return curY
  }

  private drawIcons(icons = [], startX: number, startY: number) {
    const { fontSize, x, width, padding } = this
    for (let i = 0; i < icons.length; i++) {
      const icon = icons[i]
      const iconX = startX + fontSize * 0.7 + i * (fontSize * 1.2)

      if (iconX + fontSize / 2 * 3 > x + width + padding) return
      this.drawIcon(icon, iconX, startY)
    }
  }

  private drawIcon(icon, startX: number, startY: number) {
    const {
      ctx,
      attrHeight,
      fontSize,
    } = this

    ctx.save()
    /**
     * 
     */
    ctx.beginPath()
    ctx.strokeStyle = icon.bdC || '#0C64EB'
    ctx.fillStyle = icon.bgC || '#ECF5FF'
    ctx.arc(startX, startY + Math.floor(attrHeight / 2) - 1, fontSize / 2, 0, 2 * Math.PI)
    ctx.fill()
    ctx.stroke()

    /**
     * 
     */
    ctx.fillStyle = icon.color || '#0C64EB'
    ctx.font = `${Math.ceil(fontSize / 1.2)}px Arial`
    // 
    ctx.textAlign = 'center'
    ctx.fillText(icon.text, startX, startY + (attrHeight - fontSize) / 2 + 1)
    ctx.restore()
  }

  private drawSplitLine(curY) {
    const {
      ctx,
      attributesPrimary,
      attrHeight,
      x, y, width: w, height: h,
    } = this
    curY += (attributesPrimary.length ? 1 : 2) * attrHeight

    ctx.save()
    if (curY < h + y) {
      ctx.beginPath()
      ctx.strokeStyle = '#000'
      ctx.moveTo(x + 0.5, curY + attrHeight / 3 + 0.5)
      ctx.lineTo(x + w + 0.5, curY + attrHeight / 3 + 0.5)
      ctx.stroke()
    }
    ctx.restore()
    return curY
  }

  private drawTreeExpandArrow ({ x, y, size, expand }) {
    const {ctx} = this

    ctx.save()
    ctx.beginPath()
    ctx.translate(x - 4, y + size / 2)
    expand && ctx.rotate(90 * Math.PI / 180)
    ctx.moveTo(-2, -4)
    ctx.lineTo(-2, 4)
    ctx.lineTo(2, 0)
    ctx.fill()
    ctx.restore()
  }

  private drawFocusAttr({ attrItem, finalText, curX, curY, fullWidth, nodePadding }) {
    // if (this.focusAttr !== attrItem) return

    const {
      ctx,
      attrFocusStyle,
    } = this
    const backgroundStartX = attrItem?.children?.length ? curX : curX - nodePadding / 2
    const backgroundWidth = attrItem?.children?.length ? fullWidth - nodePadding / 2 : fullWidth

    ctx.save()
    ctx.fillStyle = attrFocusStyle.backgroundColor
    ctx.fillRect(backgroundStartX, curY - 1, backgroundWidth, 16)

    ctx.fillStyle = attrFocusStyle.color
    ctx.fillText(finalText, curX, curY)

    ctx.restore()
  }

  // 
  private attributesPartition () {
    this.attributesPrimary = []
    this.attributesPrimaryNon = []

    const { attributes, attributesPrimary, attributesPrimaryNon } = this
    for (let i = 0; i < attributes.length; i++) {
      const item = attributes[i]
      item.primaryKey ? attributesPrimary.push(item) : attributesPrimaryNon.push(item)
    }
  }

  private calcBoundingBox() {
    const {
      x, y,
      width, height,
    } = this

    this.boundingBox = [
      [x, y],
      [x + width, y],
      [x, y + height],
      [x + width, y + height],
    ]
  }
}
