/**
 * reverse
 *  
 *  
 */
type Action = {
  user?: string
  time?: Date
  targetId?: string
  operationType?: string
  reverse: () => any
}

export class History {
  private history: Action[]
  private currentIndex: number
  private maxSteps: number
  private compressTime: number

  constructor(maxSteps = 20) {
    this.history = []
    this.currentIndex = -1
    this.maxSteps = maxSteps
    // 
    this.compressTime = 200
  }

  public push(action: Action | Action["reverse"]) {
    // 
    if (typeof action === 'function') {
      const reverse = action
      action = {
        reverse,
      }
    }
    if (!action.time) action.time = new Date()

    // 
    this.history.splice(this.currentIndex + 1)

    // 
    // if (this.currentIndex >= 0) {
    //   const prevAction = this.history[this.currentIndex]
    //   if (this.canMerge(prevAction, action)) {
    //     // 
    //     prevAction.reverse = () => {
    //       (action as Action).reverse()
    //       // prevAction.reverse()
    //     }
    //     prevAction.time = action.time
    //     return
    //   }
    // }

    this.history.push(action)
    this.currentIndex++

    if (this.history.length > this.maxSteps) {
      this.history.splice(0, 1)
      this.currentIndex--
    }
  }

  private canMerge(prevAction: Action, nextAction: Action) {
    const timeDistance = Number(nextAction.time) - Number(prevAction.time)
    const ifTimeLegal = timeDistance <= this.compressTime
    const ifSameTarget = prevAction.targetId === nextAction.targetId
    const ifSameOperation = prevAction.operationType === nextAction.operationType
    return ifTimeLegal && ifSameTarget && ifSameOperation
  }

  private canUndo() {
    return this.currentIndex >= 0
  }

  private canRedo() {
    return this.currentIndex < this.history.length - 1
  }

  public undo() {
    if (this.canUndo()) {
      const action = this.history[this.currentIndex]
      this.currentIndex--
      return action
    }
    return null
  }

  public redo() {
    if (this.canRedo()) {
      this.currentIndex++
      const action = this.history[this.currentIndex]
      return action
    }
    return null
  }

  public clear() {
    this.history = []
    this.currentIndex = -1
  }

  public getHistory() {
    return this.history
  }

  public getCurrentIndex() {
    return this.currentIndex
  }

  public getMaxSteps() {
    return this.maxSteps
  }

  public setMaxSteps(maxSteps: number) {
    this.maxSteps = maxSteps
    if (this.history.length > this.maxSteps) {
      this.history.splice(0, this.history.length - this.maxSteps)
      this.currentIndex -= this.history.length - this.maxSteps
    }
  }
}
